import styled from '@emotion/styled';

export const ButtonStyle = styled.button`
padding: 15px;
margin-left: 15px;
background-color: white;
font-size: 20px;
border-radius: 40%;
color: green;
font-weight: 700;
&:hover {
  background-color: while;
}
&:active {
  background-color: yellow;
}
`;